.ContactSection {
    height: auto;
    min-height: 90vh;
    padding-top: 4rem;
}

.ContactInformation h1 {
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    margin: 1rem 0;
    color: rgb(41, 100, 138);
}
.ContactInformation p {
    margin: 1rem auto;
    font-size: 1.1rem;
    width: 80%;
    text-align: center;
    color: #32aedc;
    font-weight: 500;
}

.Icon {
    display: none;
}

@media only screen and (min-width: 1024px) {
    .ContactSection {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 8rem;
    }

    .ContactInformation h1 {
        margin-right: 5.5rem;
    }

    .Wrapper {
        display: flex;
        width: 80vw;
        justify-content: space-around;
        align-items: center;
    }
    
    .ContactSection {
        position: relative;
    }

    .ClipPath {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        clip-path: polygon(0 0, 100% 0, 100% 100%);
        background-color: rgba(243, 246, 250, 0.7);
        z-index: -1;
    }

    .Icon {
        display: block;
        height: 25rem;
    }
}
